import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Styles-module
import style from '../styles';
// Types
interface IDescPeriodProps {
  start: string | number;
  ending: string | number;
  diplomas: boolean;
}

const DescPeriod: React.FC<IDescPeriodProps> = ({
  start,
  ending,
  diplomas,
}) => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <p className={style.desc(diplomas)}>
      <strong>
        {lang.bool ? 'Год поступления' : 'Year of admission'}:{' '}
        <time dateTime={String(start)}>{start}</time>
      </strong>
      <br />
      <strong>
        {lang.bool ? 'Год окончания' : 'Year of ending'}:{' '}
        <time dateTime={String(ending)}>{ending}</time>
      </strong>
    </p>
  );
};

export default DescPeriod;
