// Utils
import { convertInString } from '../../../utils/js/сonvert';
import { activeColor } from '../../../utils/ts/activeColor';
// Styles-module
import education from './education.module.scss';

const item = convertInString(
  education.item,
  ...activeColor('borderColor-1', 'gradient', 'boxShadow-1')
);
const desc = (lastchild: boolean) =>
  convertInString(
    education.desc,
    lastchild && education.desc__last,
    'active-color-1'
  );
const speciality = convertInString(
  education.desc__speciality,
  'active-textShadow-1'
);
const video = (index: number) =>
  convertInString(education.video, education[`video-${index + 1}`]);
const link = convertInString(
  education.diploma,
  ...activeColor('color-1', 'background-focus')
);
const line = convertInString(education.item__line, 'active-background-1');
const indicator = convertInString(
  education['item__line-indicator'],
  ...activeColor('borderColor-1', 'background-2')
);
const diplomaItem = convertInString(education.diploma__item, 'active-color-1');

const style = {
  item,
  desc,
  speciality,
  video,
  link,
  line,
  indicator,
  diplomaItem,
};
export default style;
