import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Components
import UsTitle from '../../Common/UsTitle';
import DescSpecialization from './DescSpecialization';
import DescPeriod from './DescPeriod';
import DiplomasList from './DiplomasList';
import VideoEducation from './VideoEducation';
// Styles-module
import education from './education.module.scss';
import style from './styles';
// Assets
import { image, video } from '../../../assets';

const Education: React.FC = () => {
  // React Context
  const { screenWidth, lang, data } = useContext(sectionContext);
  // Check Screen Size
  const screenSize = screenWidth <= 1138;
  // Play video on hover
  const toggleVideo = (
    event: React.MouseEvent<HTMLDivElement>,
    children: string,
    act: string = 'play'
  ) => {
    const videoEl = event.currentTarget.children?.namedItem(
      children
    ) as HTMLVideoElement;

    return act === 'pause' ? videoEl.pause() : videoEl.play();
  };
  // Create us-title
  const title = (value: string): JSX.Element => (
    <UsTitle level={2} cssClass={education.title} content={value} />
  );

  return (
    <ul className={education.list}>
      {(data as DataEducation[])
        .map((o) => ({
          ...o,
          video: video.education[o.name],
          diplomas: image.education.diplomas[o.name],
        }))
        .map((o, i) => (
          <li
            key={`education-${i + 1}`}
            className={style.item}
            style={{
              animationDuration: `${((i + 2) / 2).toFixed(1)}s`,
            }}
          >
            <div
              className={education.content}
              onMouseOver={(e) => o.video && toggleVideo(e, o.name, 'play')}
              onMouseOut={(e) => o.video && toggleVideo(e, o.name, 'pause')}
            >
              {title(
                lang.bool
                  ? 'Образовательное учреждение'
                  : 'Educational institution'
              )}
              <p className={style.desc(false)}>{o.institution[lang.code]}</p>
              {title(lang.bool ? 'Специализация' : 'Specialization')}
              <DescSpecialization
                specialization={o.specialization[lang.code]}
                speciality={o.speciality[lang.code]}
              />
              {title(lang.bool ? 'Период обучения' : 'Period of study')}
              <DescPeriod
                start={o.start}
                ending={o.ending}
                diplomas={!o.diplomas}
              />
              {o.diplomas && (
                <>
                  {title(
                    lang.bool ? 'Диплом / Сертификат' : 'Diploma / Certificate'
                  )}
                  <DiplomasList diplomas={o.diplomas} />
                </>
              )}
              <VideoEducation index={i} id={o.name} {...o.video} />
            </div>
            {!screenSize && (
              <span
                className={style.line}
                children={<span className={style.indicator} />}
              />
            )}
          </li>
        ))}
    </ul>
  );
};

export default React.memo(Education);
