import React from 'react';
// Styles-module
import education from '../education.module.scss';
import style from '../styles';
// Types
interface IDiplomasListProps {
  diplomas: ISourceDiplomas[];
}

const DiplomasList: React.FC<IDiplomasListProps> = ({ diplomas }) => {
  return (
    <ul className={education.diploma__list}>
      {diplomas.map((obj, i) => (
        <li
          key={`education-diploma-${i + 1}`}
          className={style.diplomaItem}
          children={
            <a
              className={style.link}
              {...obj}
              target="blank"
              children={obj.name}
            />
          }
        />
      ))}
    </ul>
  );
};

export default DiplomasList;
